.gallery {
  margin-bottom: 50px;

  img {
    transition: 0.5s;
    width: 100%;
    height: 100%;
  }

  .items {
    padding: 15px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    text-align: center;
  }

  h3 {
    margin: 10px 0 10px 0;
    font-weight: 400;
  }

  .img {
    position: relative;
    overflow: hidden;
    width: 300px;
    height: 252px;
  }

  i {
    position: absolute;
    top: 45%;
    left: 45%;
    z-index: 5;
    font-size: 40px;
    color: white;
    opacity: 0;
    transition: 0.5s ease-in-out;
  }
  .img::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 380px;
    height: 252px;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: 0.5s ease-in-out;
  }
  .img::after {
    content: "";
    position: absolute;
    top: 10px;
    left: 10px;
    width: 360px;
    height: 232px;
    z-index: 3;
    border: 1px solid #31b675;
    opacity: 0;
    transition: 0.5s ease-in-out;
  }

  .items:hover img {
    transform: scale(1.2);
    cursor: pointer;
  }
  .items:hover i,
  .items:hover .img::after,
  .items:hover .img::before {
    opacity: 1;
    cursor: pointer;
  }
}

.popup {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
  z-index: 8;

  button {
    background-color: #fff;
    padding: 20px 20px;
    color: #000;
    position: absolute;
    top: -7%;
    right: -34.5%;
    font-weight: bold;
  }
}

.hide {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
}

.popup-content {
  position: absolute;
  top: 15%;
  left: 20%;
}

@media screen and (max-width: 768px) {
  .gallery {
    .container {
      max-width: 100%;
    }

    .img {
      width: 100%;
      height: 200px;
    }

    .img::after {
      width: 300px;
      height: 180px;
    }
  }

  .popup-content {
    padding: 50px;
    position: absolute;
    top: 30%;
    left: 0%;
  }

  .popup button {
    background-color: #fff;
    padding: 20px 20px;
    color: #000;
    position: absolute;
    top: -2%;
    right: 6.5%;
    font-weight: bold;
  }
}
